@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&family=Sarala:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.area-chart {
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: relative;
  height: 364px;
  width: 716px;
  background-color: #ffffff;
  border-radius: 14px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
  margin-bottom: 10px;
}

.area-chart .header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 20px;
}

.area-chart .header .title {
  font-family: Poppins;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 27px;
}

.area-chart .header select {
  background: #FCFDFD;
  border: 0.6px solid #D5D5D5;
  box-sizing: border-box;
  border-radius: 4px;
  font-family: Sarala;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 10px;
  color: #999999;
}

.area-chart .header select:hover {
  background: #ECEDED;
}

.bar-chart {
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: relative;
  height: 516px;
  width: 1080px;
  background-color: #ffffff;
  border-radius: 14px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
}

.bar-chart .header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 20px;
}

.bar-chart .header .title {
  font-family: Poppins;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 27px;
}

.bar-chart .header select {
  background: #FCFDFD;
  border: 0.6px solid #D5D5D5;
  box-sizing: border-box;
  border-radius: 4px;
  font-family: Sarala;
  font-style: normal;
  font-weight: normal;
  font-size: 12px;
  line-height: 10px;
  color: #999999;
}

.bar-chart .header select:hover {
  background: #ECEDED;
}

.bar-chart .brands-yaxis-label {
  font-family: Sarala;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
}

.bar-chart .brands-data-label {
  font-family: Sarala;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
}

body {
  background-color: #E5E5E5;
  height: 100ch;
  width: 100%;
  padding: 10px;
  overflow: hidden;
}
